<template>
  <div class="box">
    <div class="marquee">
      <div class="marquee_title">
        <span>中奖快讯：</span>
      </div>
      <div class="marquee_box">
        <ul class="marquee_list" :class="{marquee_top:animate}">
          <li v-for="(item, index) in marqueeList">
            <span>{{item.name}}</span>
            <span>获得了</span>
            <span class="red"> {{item.gift}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    name: "Marquee",
    data() {
      return {
        animate: false,
        id: this.activityId,
        marqueeList: []
      }
    },
    props: ['activityId'],
    mounted() {
      axios.post(this.proxy + 'queryLatestPrizes.do',{}).then(res=>{
        if(res.status === 200 && res.data.error_no == '0'){
          let data =res.data.data;
          for(var i = 0; i < data.length; i++){
            this.marqueeList.push({name:data[i].nicname,gift:data[i].name})
          }
        }
      })
    },
    created: function () {
      setInterval(this.showMarquee, 2000)
    },
    methods: {
      showMarquee: function () {
        if(this.marqueeList.length == 1){
          return;
        }
        this.animate = true;
        setTimeout(() => {
          this.marqueeList.push(this.marqueeList[0]);
          this.marqueeList.shift();
          this.animate = false;
        }, 500);

      }
    },

  }

</script>

<style scoped lang="stylus">
  div, ul, li, span, img {
    margin: 0;
    padding: 0;
    display: flex;
    box-sizing: border-box;
  }

  .box {
    width 100%
    height 100%
  }

  .marquee {
    width 100%
    height 100%
    align-items: center;
    color: #fff;
    display: flex;
    box-sizing: border-box;
  }

  .marquee_title {
    height: 100%;
    padding-left .2rem
    font-size: .14rem;
    align-items: center;
  }

  .marquee_box {
    display: block;
    position: relative;
    width: 70%;
    height: 100%;
    overflow: hidden;
  }

  .marquee_list {
    width 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }

  .marquee_top {
    transition: all 0.5s;
    margin-top: -.44rem
  }

  .marquee_list li {
    width 100%
    height: .44rem;
    line-height: .44rem;
    overflow hidden
    white-space nowrap
    text-overflow ellipsis
    font-size .18rem
  }

  .marquee_list li span {
    padding: 0 2px;
  }

  .red {
    color: #fff;
  }

</style>



