<template>
  <div class="sign-box">
    <div class="sign-bg">
      <p class="sign-tip">请填写您的登录口令</p>
      <input v-model="code" class="sign-input" type="text" placeholder="在此粘贴登录口令">
      <div class="button-box">
        <button class="empty" v-on:click="empty">清空重填</button>
        <button class="submit" v-on:click="submit">确认并提交</button>
      </div>
      <div class="line"></div>
      <p class="no-password">没有口令?</p>
      <img src="../assets/sign/sign_qrcode_2.png" class="qr-code"></img>
      <div class="bottom-tip">
        <p>长按识别二维码关注</p>
        <p>{{title}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import qs from 'qs';

  export default {
    name: "SignIn",
    data() {
      return {
        code: '',
        title: '',
      }
    },
    props: {
      isLogin: {
        type: Function
      }
    },
    mounted() {
      let href = location.href;
      if (href.indexOf('cangbaotu') != -1) {
        this.title = '点击菜单栏中“登录口令”，获取专属口令!'
      }
      // if (href.indexOf('nianhuo') != -1) {
      //   this.title = '公众号内回复年货,获得您的专属口令!'
      // }
      if (href.indexOf('qiandao') != -1){
        this.title = '点击菜单栏中“登录口令”，获取专属口令!'
      }
    },
    methods: {
      submit() {
        let data = {code: this.code}
        if(this.code == ''){
          alert('请输入正确的校园口令!')
        }else{
          axios.post(this.proxy + 'loginCode.do', qs.stringify(data)).then((res) => {
            if (res.status == 200) {
              if (res.data.error_no == '0') {
                this.isLogin();
              }
              if (res.data.error_no == '1') {
                alert(res.data.data);
              }
            }
          })
        }
      },
      empty() {
        this.code = '';
      }
    }
  }
</script>

<style scoped lang="stylus">
  $darkRed = #b10000;
  .sign-box {
    position fixed
    top 0
    bottom 0
    right 0
    left 0
    background rgba(0, 0, 0, 0.5);
    z-index 999999
    .sign-bg {
      width 7rem
      height 8rem
      background url("../assets/sign/sign_bg_2.png") no-repeat center center
      background-size cover
      position absolute
      top 0
      left 0
      bottom 0
      right 0
      margin auto
      .sign-tip {
        text-align center
        font-size .38rem
        margin-top .65rem
      }
      .sign-input {
        width 5.3rem
        height .63rem
        display block
        margin 0 auto
        margin-top .45rem
        border-radius 5px
        border 1px solid $darkRed
        outline none
        text-align center
      }
      .button-box {
        width 5.5rem
        height 1.5rem
        display flex
        align-items center
        margin 0 auto
        justify-content space-around
        button {
          border none
          width 2.45rem
          height .6rem
          margin 0;
          padding 0;
          outline none
          color #ffffff
          display block
          border-radius 4px
        }
        .submit {
          background #e02e24
        }
        .empty {
          background #ad9e99
        }
      }
      .line {
        width 5rem
        border-bottom 1px dashed #3a190c
        margin 0 auto
      }
      .no-password {
        color #3a190c
        text-align center
        font-size .45rem
        margin-top .3rem
      }
      .qr-code {
        display block
        width 1.55rem
        height 1.55rem
        margin 0 auto
        margin-top .35rem
      }
      .bottom-tip {
        margin-top .1rem
        p {
          text-align center
          font-size .28rem
          color #3a190c
        }
      }
    }
  }
</style>



