<template>
  <div>
    <div v-if="this.is_weixin()" class="container">
      <SignIn v-show="hasLogin" :isLogin="login"></SignIn>
      <div class="head"></div>
      <div class="my-integral">
        <div><span>我的积分：</span><a>{{myScore}}</a></div>
        <router-link to="/lotteryGift">
          <p>获奖记录</p>
        </router-link>
      </div>
      <!--<div class="badge">-->
      <!--<img :src="badge" alt="">-->
      <!--</div>-->
      <div class="marquee-box">
        <div class="marquee">
          <marquee :activityId="activityId"/>
        </div>
      </div>
      <div class="list-box">
        <div class="list">
          <lottery :beforeLottery="beforeLottery" :score="myScore" :lotteryBtn="lotteryBtn" :flag="flag" :prizesList="prizesList" :prize="prize"
                   :afterLottery="afterLottery"  :thanksLottery="thanksLottery"></lottery>
        </div>
      </div>
      <div class="bottom">
        <!--<img src="../../assets/campus.png" alt="">-->
        <div>
          每次抽奖活动消耗<span style="color:#eace49"> 1 </span>积分
        </div>
        <img src="../../assets/jifenchoujiang/jfcj-xiaoyuan-logo.png" alt="">
      </div>
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
      <p style="font-size:20px;text-align: center;margin-bottom:.2rem">关注百度校园公众号</p>
      <img src="../../lib/qrCode.png" alt="">
    </div>
  </div>
</template>

<script type="text/javascript">
  import lottery from '@/components/gerenchoujiang/lotteryPage.vue';
  import marquee from '@/components/gerenchoujiang/Marquee.vue'
  import SignIn from '../../lib/SignIn'
  import axios from 'axios'

  export default {
    data() {
      return {
        prize: 0,
        // badge: '',
        prizesList: [],
        lotteryBtn: {
          img: require('../../assets/icon-lottery-btn.png')
        },
        thanksLottery: {
          img: require('../../assets/smile.png')
        },
        activityId: this.getQueryParams('id'),
        winData: {},
        isWin: false,
        hasLogin: false,
        alertMessage: '',
        myScore:null,
        flag:true,
      };
    },
    mounted() {
      this.queryGoods();
      this.queryScore();
    },
    methods: {
      queryScore() {
        axios.post(this.proxy + 'queryMyScore.do').then(res => {
          if (res.status === 200 && res.data.error_no == '0') {
            this.myScore = res.data.data;
          }
        })
      },
      queryGoods() {
        axios.post(this.proxy + 'queryDrawLotteryGoods.do').then(res => {
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              this.prizesList = res.data.data;
              // this.badge = res.data.data.pic_url;
            } else if (res.data.error_no == '2') {
              this.hasLogin = true
            } else {
              // alert(res.data.data.message);
            }
          }
        })
      },
      beforeLottery(resolve, reject) {
        let _that = this;
        this.flag = false;
        axios.post(this.proxy + 'drawALottery.do').then(res => {
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              //中奖之后
              this.isWin = true;
              this.winData = res.data.data.goodsEntity;
              for(var i = 0; i < this.prizesList.length; i++){
                if(this.prizesList[i].id == this.winData.id){
                  this.prize = i;
                }
              }
              resolve();
            } else if (res.data.error_no == '1') {
              //未中奖
              if (res.data.data.code == '100018') {
                this.prize = 7;
                this.alertMessage = res.data.data.message;
                this.isWin = false;
                resolve();
              }
              //积分不足
              if (res.data.data.code == '100017') {
                alert(res.data.data.message);
              }
            }
          }
          // if (res.status == 200 && res.data.error_no == '0') {
          //   let data = res.data.data;
          //   if (data.is_win === 1) {
          //     let winId = data.gift_id;
          //     this.isWin = 1;
          //     for (var i = 0; i < this.prizesList.length; i++) {
          //       if (this.prizesList[i].id === winId) {
          //         this.winData = this.prizesList[i];
          //         this.prize = i;
          //         resolve();
          //       }
          //     }
          //   } else {
          //     this.isWin = 0;
          //     this.prize = 7;
          //     resolve();
          //   }
          // } else if (res.data.error_no == '1') {
          //   //已经参加过活动
          //   if (res.data.data.code == '100012') {
          //     alert(res.data.data.message);
          //     //已经抽过奖，
          //     //判断是否中奖
          //     axios.post(this.proxy + 'queryLuckHistory.do?activityId=' + this.activityId, {}).then(res => {
          //       if (res.status === 200 && res.data.error_no == '0') {
          //         this.isWin = res.data.data.is_win; //中奖状态
          //         if (this.isWin == 1) {
          //           this.$router.push({name: 'lotteryGift', params: {id: this.activityId}})
          //         }
          //       }
          //     })
          //     //活动已经结束
          //   } else if (res.data.data.code == '100010') {
          //     alert(res.data.data.message);
          //     axios.post(this.proxy + 'queryLuckHistory.do?activityId=' + this.activityId, {}).then(res => {
          //       if (res.status === 200 && res.data.error_no == '0') {
          //         this.isWin = res.data.data.is_win; //中奖状态
          //         if (this.isWin == 1) {
          //           this.$router.push({name: 'lotteryGift', params: {id: this.activityId}})
          //         }
          //       }
          //     })
          //   } else {
          //    alert(res.data.data.message);
          //   }
          // }
        })
      },
      afterLottery() {
        //是否中奖 1：中奖 0：未中奖
        //第一次参与抽奖
        this.queryScore();
        if (this.isWin) {
          alert('恭喜您获得了' + this.winData.name + '!');
          this.$router.push({path:'/lotteryGift'});
        } else {
          alert(this.alertMessage);
        }
        this.flag = true;
      },
      getQueryParams: function (name, url) {
        if (!url) url = location.href
        name = name.replace(/[\[]/, "\\\[").replace(/[\]]/, "\\\]");
        var regexS = "[\\?&]" + name + "=([^&#]*)";
        var regex = new RegExp(regexS);
        var results = regex.exec(url);
        return results == null ? null : results[1];
      },
      login() {
        window.location.href = location.href + '?time=' + ((new Date()).getTime());
        location.reload();
      }
    },
    components: {
      lottery: lottery,
      marquee: marquee,
      SignIn: SignIn,
    }
  };
</script>

<style type="text/css" scoped lang="stylus">
  .container {
    position absolute
    top 0
    left 0
    bottom 0
    right 0
    background url("../../assets/jifenchoujiang/jfcj-bg.png") no-repeat center center
    background-size cover
    overflow hidden
    .head {
      width 7.5rem
      height 2.7rem
      margin 0 auto
      margin-top .1rem;
      background url("../../assets/jifenchoujiang/jfcj-choudajiang.png") no-repeat center center
      background-size cover
    }
    .my-integral {
      width 5.74rem
      height auto
      overflow hidden
      margin 0 auto
      div {
        font-size .2rem
        color #fff
        float left
        line-height .5rem
        a {
          color #eace49
          font-size .2rem
        }
      }
      p {
        float right
        font-size .2rem
        border-radius 20px
        background #f1346c
        color #fff
        width 1.8rem
        height .5rem
        text-align center
        line-height .5rem

      }
    }
    .adge {
      width 5.55rem
      height 1.08rem
      position absolute
      top 2.25rem
      left .03rem
      right 0
      margin-right auto
      margin-left auto
      border-radius 5px
      box-sizing border-box
      overflow hidden
      img {
        width 100%
        object-fit cover
      }
    }
    .list-box {
      width 6.74rem
      height 6.48rem
      margin 0 auto
      background: url("../../assets/board02.png") no-repeat center center
      -webkit-background-size: cover
      background-size: cover
      position relative
    }
    .list {
      width: 5.9rem;
      height: 5.47rem;
      position absolute
      top: 0
      left 0
      bottom .15rem
      right 0
      margin auto;
    }
    .marquee-box {
      width 5.74rem
      height .94rem
      background url("../../assets/board.png") no-repeat center center
      background-size cover
      margin 0 auto
      margin-top .25rem;
    }
    .marquee {
      width 5.5rem
      height: .44rem
      position absolute
      top 3.65rem
      left 1.01rem
    }
    .bottom {
      margin 0 auto
      margin-top .5rem
      font-size .3rem
      color #ffffff
      width 100%
      text-align center
      overflow hidden
      img {
        display: block
        width 1.82rem
        height .5rem
        margin 0 auto
        margin-top .4rem
      }
    }
    .info-box{
      position absolute
      top 0
      left 0
      right 0
      bottom 0
      background rgba(0,0,0,0.5)
      z-index: 999
      .phone {
        margin 0 auto
        width 6.5rem
        font-size: .12rem
        padding .1rem 0
        display flex
        align-items center
        justify-content center
        span {
          padding-left .2rem
        }
        input {
          border 1px solid rgba(0, 0, 0, 0.8)
          outline none
          width 3.77rem
          height .38rem;
          padding-left .1rem
        }
        .address-area {
          border 1px solid #000;
          outline: none;
          width 4rem
          height 1rem
        }
      }
    }
  }
</style>



