<template>
  <div class="iv-lottery">
    <ul>
      <li class="row">
        <div :class="['left' , {active : activeClass[index]}]" v-for="(prize,index) in list1">
          <img :src="prize.picResUrl">
          <p>{{prize.name}}</p>
        </div>
      </li>
      <li class="row">
        <!--谢谢参与-->
        <div :class="['thanks' , {active : activeClass[7]}]">
          <img :src="thanksLottery.img" alt="">
          <p>谢谢参与</p>
        </div>
        <div class="left start" @click="startLottery">
          <span>立即</span>
          <span>抽奖</span>
        </div>
        <!--2-1-->
        <div :class="['left' , {active : activeClass[3]}]" v-for="(prize,index) in list3">
          <img :src="prize.picResUrl">
          <p>{{prize.name}}</p>
        </div>
      </li>
      <li class="row">
        <div :class="['left' , {active : activeClass[6-index]}]" v-for="(prize,index) in list2">
          <img :src="prize.picResUrl">
          <p>{{prize.name}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>


<script>
  export default {
    name: 'lottery',
    data() {
      return {
        activeClass: [false, false, false, false, false, false, false, false],
        index: -1,
        count: 8,
        timer: null,
        times: 0,
        speedData: 100,
        lock: false,
        speed: 100,
        cycle: 20,
        afterLotteryHandler: null
      };
    },

    props: {
      //图片列表
      prizesList: {
        type: Array,
        default() {
          return [];
        }
      },
      //开始按钮
      lotteryBtn: {
        type: Object,
        default() {
          return {
            img: ''
          };
        }
      },
      thanksLottery: {
        type: Object,
        default() {
          return {
            img: ''
          }
        }
      },
      beforeLottery: {
        type: Function,
        default() {
          throw new Error('you must define beforeLottery before draw a lottery ');
        }
      },
      afterLottery: {
        type: Function,
        // eslint-disable-next-line
        default() {
          console.warn('you can use afterLottery after rolling');
        }
      },
      prize: {
        type: Number,
        default: 0
      },
      flag:{
        type:Boolean,
      },
      score:{
      }
    },
    computed: {
      list1() {
        return this.prizesList.slice(0, 3);
      },
      list2() {
        return this.prizesList.slice(4, 7).reverse();
      },
      list3() {
        return this.prizesList.slice(3, 4);
      },
      // myScore(){
      //   return this.score;
      // }
    },
    created() {
      this.speedData = this.speed;
      this.afterLotteryHandler = this.afterLottery;
    },
    methods: {
      startLottery() {
        if(this.score > 0){
          if(this.flag){
            let promise = () => {
              return new Promise((resolve, reject) => {
                this.beforeLottery(resolve, reject);
              });
            };
            let start = async () => {
              try {
                await promise();
                if (!this.lock) {
                  this.lock = true;
                  this.roll();
                }
              } catch (e) {
                e()
              }
            };
            start();
          }
        }else{
          alert('积分不足！')
        }

      },
      _rollHandler() {
        var index = this.index;
        var count = this.count;

        for (let i = 0, len = this.activeClass.length; i < len; i++) {
          this.activeClass[i] = false;
        }
        index += 1;
        if (index > count - 1) {
          index = 0;
        }
        this.activeClass[index] = true;
        this.index = index;
        return false;
      },
      roll() {
        // eslint-disable-next-line
        this.activeClass = this.activeClass.map(item => item = false);
        this.times += 1;
        this._rollHandler();
        if (this.times > this.cycle + 10 && this.prize == this.index) {

          clearTimeout(this.timer);

          setTimeout(
            () => {
              this._showResult();
            }
            , 1000);

          this.lock = false;
          this.index = -1;
          this.count = 8;
          this.timer = null;
          this.speedData = this.speed;
          this.times = 0;

        } else {
          if (this.times < this.cycle) {
            this.speedData -= 2;
          } else {
            if (this.times > this.cycle + 10 && ((this.prize == 0 && this.index == 7) || this.prize == this.index + 1)) {
              this.speedData += 110;
            } else {
              this.speedData += 20;
            }
          }
          if (this.speedData < 40) {
            this.speedData = 40;
          }
          this.timer = setTimeout(
            () => {
              this.roll();
            }
            , this.speedData);
        }
        return false;
      },
      _showResult(str) {
        this.afterLotteryHandler(str);
      }
    }
  };
</script>
<style scoped lang="stylus">
  .iv-lottery {
    width 100%
    height 100%
    ul {
      width 100%
      height 100%
      box-sizing: border-box;
      position relative
      margin: 0 auto
      display flex
      align-items center
      justify-content space-around
      flex-wrap wrap
      li {
        width 100%
        overflow: hidden;
        position: relative;
        display flex
        align-items center
        justify-content space-around
        .start {
          font-size .5rem;
          color #ffffff
          background-color #ff4647
          border: .2rem solid orange;
          box-sizing border-box
          border-image: url("../../assets/lottery-gift-bg.png") 20 round;
          span {
            display block
            &:first-child {
              /*margin-top .2rem*/
            }
            &:last-child {
              margin-bottom .2rem
            }
          }
          display flex
          align-items center
          justify-content center
          flex-wrap wrap
        }
        .thanks {
          display flex
          justify-content center
          align-items center
          img {
            width .9rem
            height .9rem
          }
        }
        div {
          width 1.95rem;
          height 1.8rem;
          box-sizing border-box
          border-radius 0.1rem
          overflow hidden
          position relative
          background #fff
          p {
            width 100%
            height 19%
            position absolute
            bottom .1rem
            left 0
            background #fff
            font-size .14rem
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            text-align center
          }
          &.active:before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index 999
            width: 100%;
            height: 100%;
            border: .2rem solid orange;
            box-sizing border-box
            border-image: url("../../assets/lottery-gift-bg.png") 20 round;
          }
          img {
            width: 100%;
            height 1.7rem
            vertical-align: middle;
            object-fit cover
          }
        }
      }
    }
  }
</style>



